import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    keyWords: '',
    isShow: false,
    isUpdate: true
  },
  mutations: {
    musicSearch(state, content) {
      state.keyWords = content
    },
    changShow(state) {
      state.isShow = true
    },
    changUpdate(state) {
      state.isUpdate = false
    }
  },
  actions: {},
  modules: {}
})