import Axios from 'axios'
const axios = Axios.create({
    baseURL: 'https://autumnfish.cn',
    timeout: 5000
});
export function searchMusic(songName) {
    return axios.get('/search?keywords=' + songName)
}
export function getMusicDetail(musiciId) {
    return axios.get('/song/detail?ids=' + musiciId)
}
export function getMusic(musiciId) {
    return axios.get('/song/url?id=' + musiciId)
}
export function getComment(musiciId) {
    return axios.get('/comment/hot?type=0&id=' + musiciId)
}
export function getMV(mvId) {
    return axios.get('/mv/url?id=' + mvId)
}