import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [{
    path: '',
    redirect: '/welcome'
  },
  {
    path: '/welcome',
    name: 'Welcome',
    component: () => import('../views/Welcome.vue')
  },
  {
    path: '/refresh',
    name: 'Refresh',
    component: () => import('../views/Refresh.vue')
  },
  {
    path: '/songdetail/:id',
    name: 'SongDetail',
    component: () => import('../views/SongDetail.vue')
  },
  {
    path: '/mv/:mvId',
    name: 'Mv',
    component: () => import('../views/Mv.vue')
  },
]

const router = new VueRouter({
  routes,
})

export default router