<template>
  <div class="main-tabbar">
    <div class="searchArea">
      <el-input
        placeholder="请输入歌手名或歌曲名"
        prefix-icon="el-icon-search"
        v-model="search"
        @change="searchSong(search)"
        v-if="$store.state.isShow"
      ></el-input>
    </div>
    <div class="song-list">
      <el-button
        plain
        v-for="item in songs"
        :key="item.name"
        class="songsItem"
        @click="songDetail(item)"
      >{{item.name}}</el-button>
    </div>
  </div>
</template>

<script>
import { searchMusic } from "../../../network/axios";
export default {
  data() {
    return {
      search: "",
      songs: [],
    };
  },
  methods: {
    searchSong(value) {
      this.$store.commit("musicSearch", this.search);
      searchMusic(value).then((res) => {
        this.songs = res.data.result.songs;
      });
    },
    refresh() {
      this.$router.replace({
        path: "/refresh",
        query: {
          t: Date.now(),
        },
      });
    },
    songDetail(item) {
      if (this.$route.path == "/welcome") {
        this.$router.replace("/songdetail/" + item.id);
      } else {
        this.$router.replace("/songdetail/" + item.id);
        this.refresh();
      }
    },
  },
  mounted() {},
  updated() {
    if (this.$store.state.isUpdate) {
      console.log(this.$store.state.keyWords);
      this.search = this.$store.state.keyWords;
      this.searchSong(this.$store.state.keyWords);
      this.$store.commit("changUpdate");
    }
  },
};
</script>

<style lang="less">
.main-tabbar {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  .el-input__inner {
    display: block;
  }
  .el-input__inner:hover {
    border-color: #e6335f;
  }

  i {
    display: block;
  }
}
.searchArea {
  line-height: 0 !important;
  position: fixed;
  top: 60px;
  left: 0;
}
.song-list {
  margin-top: 40px;
}

.el-button {
  width: 100%;
  background-color: #085f95 !important;
  border: 1px solid #ffffff !important;
  color: #ffffff !important;
  margin-top: 8px !important;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 0 !important;
  overflow: hidden;
}
.el-button:hover {
  background-color: white !important;
  color: #085f95 !important;
}
.el-button + .el-button {
  margin-left: 0 !important;
}
</style>